.item {
  height: 100vh;
  width: 100vw;
  background-position: center;
  display: flex;
  justify-content: center;
  background-size: cover;
  background-repeat: no-repeat;
  scroll-snap-align: start;
}

.item__container {
  display: flex;
  width: 40vw;
  flex-direction: column;
  align-items: center;
}

.item__text {
  margin-top: 15vh;
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  font-size: 36px;
  color: rgb(57, 60, 65);
  font-weight: 600;
  line-height: 48px;
  opacity: 1;
  min-width: 600px;
}

.item__desc {
  font-size: 14px;
  color: rgb(92, 94, 98);
  font-weight: 400px;
  letter-spacing: -0.5px;
  line-height: 19.796px;
  min-height: 27px;
}

.item__buttons {
  display: flex;

  justify-content: space-between;
}

.lower__thirdpart {
  margin-top: 65.1%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.item__expand {
  color: white !important;
  margin-top: 3px;
  font-size: 14px !important;
  font-weight: 400 !important;
}
